import React ,{useState} from "react";
import InputField from "./inputField";

const MovieForm = ({addMovie}) => {
    const [movieTitle, setMovieTitle] = useState('');
    const [movieYear, setMovieYear] = useState('');
    const [titleError, setTitleError] = useState(' ');
    const [yearError, setYearError] = useState(' ');
    const [nextId, setNextId] = useState(0);
    const resetForm = () => {
      setNextId(nextId+1);
      setMovieTitle('');
      setMovieYear('');
    }
    const validataForm = ()=> {
        // resetError();
        let validated = true;
        if(!movieTitle){
            setTitleError(' ');
            alert(titleError);
            validated = false;
        }
        if(!movieYear){
            setYearError(' ');
            alert(yearError);
            validated = false;
        }
        return validated;
    }
    const resetError = ()=> {
        setTitleError('');
        setYearError('');
    };
    const onSubmit = (event)=>{
      event.preventDefault();
      if(validataForm()){
        addMovie({
            id:nextId,
            title:movieTitle,
            year:movieYear
          });
          resetError();
        }
        resetForm(); 
      };
 
    return (
          <form onSubmit={onSubmit}>
            <InputField
              type="text"
              value={movieTitle}
              placeholder=""
              onChange={e => setMovieTitle(e.target.value)}
            ></InputField>
            <InputField
              type="number"
              value={movieYear}
              placeholder=""
              onChange={e => setMovieYear(e.target.value)}
            ></InputField>
            <p><button type='submit'></button></p>
          </form>
    );
};

export default MovieForm;